import React, { useState, useEffect } from 'react';
import './Render.css';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Tooltip from '@material-ui/core/Tooltip';
import Menu from '@material-ui/core/Menu';

import { mainWindow } from '../../../App'

const width1 = mainWindow.current.offsetWidth>800? 240: 120;
const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 500,
    },
    urls: {
        margin: theme.spacing.unit,
        width: 500,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
    textFieldSmall: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width1
    },
    menu: {
        width: 200,
    }
});

const CutImage  = (
    (props)=>{
        let {seats, price, index, cashbox, setSeats, addItem} = props;
        const { classes } = props;
        let [show, setShow] = useState(false);
        let statuses = ['block', 'free', 'sold']
        let [status, setStatus] = useState('');
        let handleStatus = (event) => {
            setStatus(event.target.value)
        };
        let [selectIndex, setSelectIndex] = useState([0,0]);
        const [anchorEl, setAnchorEl] = useState([]);
        let closeAnchorEl = ()=>{
            let _anchorEl = []
            for (let i = 0; i < seats.length; i++) {
                _anchorEl[i]=[]
                for (let i1 = 0; i1 < seats[i].length; i1++) {
                    _anchorEl[i][i1]=false
                }
            }
            setAnchorEl(_anchorEl)

        }
        let handleAnchorEl = (i11, i22)=>{
            let _anchorEl = anchorEl
            _anchorEl[i11][i22]=!_anchorEl[i11][i22]
            setAnchorEl(_anchorEl)

        }
        useEffect(async()=>{
            if(seats!==undefined){
                let _anchorEl = []
                for (let i = 0; i < seats.length; i++) {
                    _anchorEl[i]=[]
                    for (let i1 = 0; i1 < seats[i].length; i1++) {
                        _anchorEl[i][i1]=false
                    }
                }
                setAnchorEl(_anchorEl)
            }
        },[seats])
        return (
            <>
            {seats!==undefined?
                    <div style={{width: ((seats[0].length)*26+150)+'px'}}>
                        {price!==undefined&&price.length>0?
                            <div className='part-div-price' style={{width: '500px'}}>
                                <div className='part-number'/>
                                <div className='part-empty' style={{background: 'red'}}/>
                                <div className='part-price'>
                                    {'Продано'}
                                </div>
                                <div className='part-empty' style={{background: 'indigo'}}/>
                                <div className='part-price'>
                                    {price + ' сом'}
                                </div>
                            </div>
                            :
                            null}
                        {seats.map((row, rowId) => {
                            return (
                                <div>
                                    <div className='part-number'>
                                        {'Ряд: ' + (row[0].name.split(':')[0].split(' ')[1])}
                                    </div>
                                    {
                                        row.map((place, placeId) => {
                                            if (place.type === 'seat') {
                                                return (
                                                    <div style={{display: 'inline-block'}}>
                                                    <Tooltip title={place.color!=='red'?'Места: '+seats[rowId][placeId].name+(place.color!=='gray'?' Цена: '+seats[rowId][placeId].price:''):'Продано'}>
                                                            <div onClick={async () => {
                                                                if(!cashbox&&index!==-1) {
                                                                    setSelectIndex([rowId, placeId]);
                                                                    setStatus(seats[rowId][placeId].status)
                                                                    setShow(true)
                                                                } else if(cashbox) {
                                                                    if(place.color!=='red') {
                                                                        await closeAnchorEl()
                                                                        await handleAnchorEl(rowId, placeId)

                                                                    } else {
                                                                        seats[rowId][placeId].status = 'free'
                                                                        seats[rowId][placeId].color = 'indigo'
                                                                        addItem(seats[rowId][placeId])
                                                                        setSeats(seats)
                                                                        }
                                                                }
                                                            }} className='part' style={{background: place.color}}>
                                                                <div className='part-text'>
                                                                    {place.name.split(':')[1].split(' ')[0]}
                                                                </div>
                                                            </div>
                                                    </Tooltip>
                                                        {anchorEl.length>0&&anchorEl[rowId][placeId]?
                                                            <>
                                                            <div className='gallery-subbackground' onClick={()=>{closeAnchorEl()}}/>
                                                            <div className='header-submenu'>
                                                                <div className='header-submenu-item' onClick={()=>{seats[rowId][placeId].priceSelect = place.price;seats[rowId][placeId].color = 'red'; seats[rowId][placeId].status = 'hold';addItem(seats[rowId][placeId]);setSeats(seats);closeAnchorEl()}}>
                                                                    Взрослый: {place.price}
                                                                </div>
                                                                {place.priceKid.length>0&&parseInt(place.priceKid)>0?
                                                                <div className='header-submenu-item' onClick={()=>{seats[rowId][placeId].priceSelect = place.priceKid;seats[rowId][placeId].color = 'red'; seats[rowId][placeId].status = 'hold';addItem(seats[rowId][placeId]);setSeats(seats);closeAnchorEl()}}>
                                                                    Детский: {place.priceKid}
                                                                </div>:null}
                                                                {place.priceStud.length>0&&parseInt(place.priceStud)>0?
                                                                    <div className='header-submenu-item' onClick={()=>{seats[rowId][placeId].priceSelect = place.priceStud;seats[rowId][placeId].color = 'red'; seats[rowId][placeId].status = 'hold';addItem(seats[rowId][placeId]);setSeats(seats);closeAnchorEl()}}>
                                                                        Студен: {place.priceStud}
                                                                </div>:null}
                                                            </div>
                                                            </>
                                                            :null}
                                                    </div>
                                                )
                                            } else
                                                return (
                                                    <div className='part-empty' style={{background: place.color}}/>
                                                )
                                        })
                                    }
                                </div>
                            )
                        })}
                    </div>
                :
                null
            }
            <Dialog
                open={show}
                onClose={()=>{setShow(false)}}
                aria-labelledby='alert-dialog-title'
                aria-describedby='alert-dialog-description'>
                <DialogContent>
                    <TextField
                        select
                        SelectProps={{
                            MenuProps: {
                                className: classes.menu,
                            },
                        }}
                        label='статус'
                        type='login'
                        className={classes.textField}
                        margin='normal'
                        value={status}
                        onChange={handleStatus}
                    >
                        {statuses != undefined?
                            statuses.map(option => (
                                <MenuItem key={option} value={option}>
                                    {option}
                                </MenuItem>
                            ))
                            :
                            null
                        }
                    </TextField>
                    <br/>
                    <div>
                        <Button variant='contained' color='primary' onClick={()=>{
                            setShow(false);
                            if(status==='block'){
                                seats[selectIndex[0]][selectIndex[1]].color='Gray'
                                seats[selectIndex[0]][selectIndex[1]].price=price;
                                seats[selectIndex[0]][selectIndex[1]].status='0';
                            } else if(status==='sold'){
                                seats[selectIndex[0]][selectIndex[1]].color='Red'
                                seats[selectIndex[0]][selectIndex[1]].status=status;
                                seats[selectIndex[0]][selectIndex[1]].price=price;
                            } else {
                                seats[selectIndex[0]][selectIndex[1]].status=status;
                                seats[selectIndex[0]][selectIndex[1]].price=price;
                                seats[selectIndex[0]][selectIndex[1]].color='Indigo';
                            }
                            setSeats(seats)
                        }} className={classes.button}>
                            Сохранить
                        </Button>
                        <Button variant='contained' color='secondary' onClick={()=>{setShow(false)}} className={classes.button}>
                            Отмена
                        </Button>
                    </div>
                </DialogContent>
            </Dialog>
            </>
        )
    }
)

CutImage.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(CutImage);
