import {sectorKinoBoxHall1} from './KinoBox/Hall1';
import {sectorKinoBoxHall2} from './KinoBox/Hall2';
import {sectorKinoBoxHall3} from './KinoBox/Hall3';
import {sectorRussiaHall1} from './Russia/Hall1';
import {sectorRussiaHall2} from './Russia/Hall2';
import {sectorRussiaHall3} from './Russia/Hall3';
import {sectorRussiaHall4} from './Russia/Hall4';
import {sectorRussiaHall5} from './Russia/Hall5';
import {sectorNurCinemaHall1} from './NurCinema/Hall1';
import {sectorNurCinemaHall2} from './NurCinema/Hall2';
import {sectorNurCinemaHall3} from './NurCinema/Hall3';
import {sectorNurCinemaHall4} from './NurCinema/Hall4';
import {sectorNurCinemaHall5} from './NurCinema/Hall5';
import {sectorKinoBoxHall4} from './KinoBox/Hall4';
import {sectorRussiaHallRussia} from './Russia/HallRussia';
import {sectorManasHallAitysh} from './Manas/HallAitysh';
import {sectorManasHallBig} from './Manas/HallBig';
import {sectorManasHallBlue} from './Manas/HallBlue';
import {sectorManasHallGreen} from './Manas/HallGreen';
import {sectorManasHallRed} from './Manas/HallRed';
import {sectorManasHallRetro} from './Manas/HallRetro';
import {sectorManasHallZamandash} from './Manas/HallZamandash';

export const getWhereDara = (name, hall) => {
    if(name==='KinoBox'){
        if(hall === 'зал 1')
            return sectorKinoBoxHall1();
        else if(hall === 'зал 2')
            return sectorKinoBoxHall2();
        else if(hall === 'зал 3')
            return sectorKinoBoxHall3();
        else if(hall === 'зал 4')
            return sectorKinoBoxHall4();
    } else if(name==='Россия'){
        if(hall === 'зал 1'){
            return sectorRussiaHall1();
        }
        else if(hall === 'зал 2')
            return sectorRussiaHall2();
        else if(hall === 'зал 3')
            return sectorRussiaHall3();
        else if(hall === 'зал 4')
            return sectorRussiaHall4();
        else if(hall === 'зал 5')
            return sectorRussiaHall5();
        else if(hall === 'зал Россия')
            return sectorRussiaHallRussia();
    } else if(name==='Нур Синема'){
        if(hall === 'зал 1'){
            return sectorNurCinemaHall1();
        }
        else if(hall === 'зал 2')
            return sectorNurCinemaHall2();
        else if(hall === 'зал 3')
            return sectorNurCinemaHall3();
        else if(hall === 'зал 4')
            return sectorNurCinemaHall4();
        else if(hall === 'зал 5')
            return sectorNurCinemaHall5();
    } else if(name==='Манас'){
        if(hall === 'зал Замандаш')
            return sectorManasHallZamandash();
        else if(hall === 'зал Айтыш')
            return sectorManasHallAitysh();
        else if(hall === 'зал Ретро')
            return sectorManasHallRetro();
        else if(hall === 'зал Большой')
            return sectorManasHallBig();
        else if(hall === 'зал Красный')
            return sectorManasHallRed();
        else if(hall === 'зал Голубой')
            return sectorManasHallBlue();
        else if(hall === 'зал Зеленый')
            return sectorManasHallGreen();
    } else if(name==='')
        return null;
}

export const getHalls = (name) => {
    if(name==='KinoBox'){
        return ['зал 1', 'зал 2', 'зал 3', 'зал 4'];
    } else if(name==='Нур Синема'){
        return['зал 1', 'зал 2', 'зал 3', 'зал 4', 'зал 5']
    } else if(name==='Россия'){
        return['зал 1', 'зал 2', 'зал 3', 'зал 4', 'зал 5', 'зал Россия']
    } else if(name==='Манас'){
        return ['зал Замандаш', 'зал Айтыш', 'зал Ретро', 'зал Большой', 'зал Красный', 'зал Голубой', 'зал Зеленый'];
    } else if(name==='')
        return [];
}